.container {
  display: flex;
  justify-content: center;
  height: 100vh;
}

.img {
  background: url('../images/image\ 1.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 60%;
  height: auto;
}

.forms {
  width: 40%;
  margin: auto;
  background-color: #05386b;
  height: 100%;
}

.forms input {
  height: 40px;
  left: 941px;
  top: 448px;
  background: white;
  border-radius: 5px;
  margin: 12px 0;
  padding-left: 14px;
  border: none;
  outline: none;
  color: black;
}

.forms h2 {
  color: white;
}

.forms .label {
  position: relative;
  color: white;
  text-align: left;
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 80%;
  font-size: 13px;
}

.forms .buttons {
  width: 80%;
  display: flex;
  margin-top: 15px;
  justify-content: center;
  background-color: transparent;
}

.forms .buttons button {
  width: 150px;
  height: 35px;
  background: #379683;
  border: none;
  outline: none;
  color: white;
  text-align: center;
  cursor: pointer;
}

.forms .write {
  display: flex;
  width: 80%;
}

.link {
  text-decoration: none;
  color: #379683;
}

.icon {
  position: absolute;
  top: 47%;
  right: 3%;
  color: black;
}

@media (max-width: 1000px) {
  .img {
    width: 50%;
  }

  .forms {
    width: 50%;
  }
}

@media (max-width: 800px) {
  .img {
    display: none;
  }

  .forms {
    width: 100%;
  }

  .container {
    overflow: scroll;
  }
}
